.Chat-container{
    width: 27%;
    position: absolute;
    border: 1px solid orange;
    height: 92%;
    right: 15px;
    bottom: 15px;
    z-index: 1;
    color: white;
    background: #1B1F38;
  }
  .Chat-row{
    height: 50px;
  }
  .Chat-column{
    height: 50px;
  }
  .Chat-body{
    padding-top: 20px;
    height: 80%;
  }
  .Chat-title{
    height: 40px;
    text-align: left;
    margin-left: 15px;
    margin-bottom: 7px;
    margin-top: 0px;

  }
  .Chat-messages{
    border: 1px solid orange;
    overflow: auto;
    height: 529px;
    position : relative;
    bottom:0;
  }
  .Chat-footer{
    position: absolute;
    width: 100%;
    bottom: 10px;
  }
  .Chat-bubble-left{
    padding: 5px 10px 5px 10px;
    text-align: left;
    border-radius: 10px;
    background: #F0F0F0;
    position : relative;
    bottom:0;
  }
  .Chat-bubble-right{
    text-align: right;
    padding: 5px 10px 5px 10px;
    background: #cce6ff;
    border-radius: 10px;
  }
  html, body {
    font: 14px/1.21 'Helvetica Neue', arial, sans-serif;
    font-weight: 400;
    box-sizing: border-box;
  }

  *, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
  }

  h1 {
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .chat-window {
    width: 412px;
    margin: auto;
    border: 1px solid #1B1F38;
  }

  .conversation-view {
    width: 100%;
    background-color: white;
    min-height: 500px;
    padding: 20px 40px;
    background-color: #1B1F38;
  }

  .message-box {
    width: 100%;
    padding: 20px 20px;
  }

  .text-input {
    width: 100%;
    border: none;
    padding: 5px;
  }

  .chat-bubble {
    font-size: 15px;
    margin-bottom: 20px;
    width: 100%;
    display: flex;
  }

  .chat-bubble.ai {
    justify-content: flex-end;
  }

  .chat-bubble.ai .chat-content {
    background-color: #1B1F38;
  }

  .chat-content {
    display: inline-block;
    padding: 8px 15px;
    background-color: #1B1F38;
    color: white;
    border-radius: 10px;
  }